<template>
  <div class="main-page">
    <div class="headerTag">我的收藏</div>
    <ScrollView ref="ScrollView" top="44px" bottom="0" @down="down" @upFun="up">
      <!--内容...-->
      <list-line
            v-for="(item, index) in content()"
            :key="index"
            :item="item"
            :diynew= true
          />
    </ScrollView>
  </div>
</template>
<script>
export default {
  data() {
    return {
      classList: [],
      content() {
        return this.dataList.map((item) => {
          return {
            img: item.CoverImg,
            title: item.SourceName,
            CreateTime: item.CreateTime,
            id: item.SourceId ,
          };
        });
      },
      dataList: [],
    };
  },
  methods: {
    BAS_CollectMy() {
      this._api.BAS_CollectMy().then((res) => {
        this.dataList = res.data;
      });
    },
    down(instance) {
      this.$refs["ScrollView"].reUp();
    },
    // 上拉加载
    up([page, instance]) {
      this._api
        .BAS_CollectMy({
          ...this.searchInfo,
          Page: page.num,
          PageSize: page.size,
          ClassId:this.name
        })
        .then((res) => {
          if (res) {
            let arr = res.data;
            if (page.num === 1) this.dataList = [];
            this.dataList = this.dataList.concat(arr);
            this.$nextTick(() => {
              instance.endSuccess(arr.length);
            });
          }
          this.loading = false;
        });
    },
  },
  created() {
    this.BAS_CollectMy();
  },
};
</script>

